.methodic-section {
  padding: 80px 0 120px;

  // .method-section__title

  &__title {
    margin: 0 auto 3rem;
    text-align: center;
    max-width: 1000px;

    h3 {
      color: var(--dark-color-06);
      font-size: 1.6rem !important;
      margin-top: 1rem;
      font-weight: 400;
    }

  }
}



.methodic {
  display: flex;
  gap: 1rem;
  justify-content: space-between;

  @include m-1070 {
    overflow-x: auto;
    // overflow-y: visible;
    padding-bottom: 3rem;
  }

  // .methodic__item

  &__item {
    flex: 1 1 25%;
    border: 1px solid var(--grey-dark-color);
    padding: 24px 30px;
    border-radius: var(--br);
    box-shadow: var(--box-shadow-min-2);
    transition: box-shadow .3s ease-in-out;

    @include hover {
      box-shadow: var(--box-shadow-min);
    }

    @include m-1070 {
      flex: 0 0 250px;
    }
  }

  // .methods__content
  &__content {
    width: 100%;

    img {
      width: 72px;
      height: 72px;
      margin-bottom: 1rem;
    }

    h3 {
      font-size: 1.1rem;
      color: var(--second-bright-color);
      margin-bottom: 0.5rem;
    }

    h4 {
      font-size: .8rem;
      letter-spacing: 1.3px;
    }

    p {
      font-size: .9rem;
      color: var(--dark-color-06);
    }
  }
}
