.faqs-section {
  padding: 80px 0;
  overflow: hidden;
  position: relative;
  background: var(--violet);
  user-select: none;
  z-index: 1;

  &::after {
    content: "";
    position: absolute;
    background: url(../img/svg/orn-8.svg) 0px 0px / contain no-repeat;
    width: 480px;
    height: 480px;
    left: -50px;
    bottom: -100px;
    transform: rotate(40deg);
    opacity: 0.1;
    user-select: none;
  }

  &::before {
    content: "";
    position: absolute;
    background: url(../img/svg/orn-9.svg) 0px 0px / contain no-repeat;
    width: 480px;
    height: 480px;
    right: -50px;
    top: -100px;
    transform: rotate(240deg);
    opacity: 0.1;
    user-select: none;
  }

  &__container {
    position: relative;
    z-index: 2;
  }
}



.faqs {
  display: flex;
  gap: 60px;
  justify-content: center;

  @include m-768 {
    display: block;
  }

  &__content {
    padding-top: 200px;

    @include m-768 {
      padding: 0 0 40px;
      text-align: center;
    }

    p,
    h1,
    h2 {
      color: var(--white-color);
      letter-spacing: 1.7px;
      max-width: 370px;
      width: 100%;

      @include m-768 {
        max-width: 400px;
        margin: 1rem auto;
      }
    }

    p {
      max-width: 300px;
      color: var(--white-color-06);

      @include m-768 {
        max-width: 400px;
        margin: 1rem auto;
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}


.arrow-down-close {
  width: 50px;
  height: 25px;
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);

  &:before,
  &:after {
    content: '';
    display: inline-block;
    height: 24px;
    width: 4px;
    background-color: var(--white-color);

    border-radius: 5px;
    position: relative;
    transition: all .2s ease-in-out;
  }

  &:before {
    transform: rotate(-45deg);
    left: 0;
    top: 0;
  }

  &:after {
    transform: rotate(45deg);
    left: 10px;
    top: 0;
  }
}

.accordion {
  &__item {
    max-width: 600px;
    width: 100%;
    margin-bottom: 20px;
  }

  &__wrapper {
    width: 100%;
    border: 2px solid var(--white-color-06);
    border-radius: 14px;
    transition: all .3s ease-in-out;

    &.open {
      background-color: var(--violet-dark);
      border: 2px solid var(--white-color);
      z-index: 1;
    }
  }
}

.accordion__btn,
.accordion__content {
  margin: 0;
  border: 0;
}

.accordion__btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 80px 20px 30px;
  cursor: pointer;
  position: relative;
  transition: max-height .2s ease-in-out, opacity .2s ease-in-out, visibility .2s ease-in-out;

  @include m-520 {
    padding: 20px 80px 20px 20px;
  }

  h3 {
    color: var(--white-color);
    font-size: clamp(1rem, .9rem + 0.5714vw, 1.1rem) !important;
    font-weight: 400;
    letter-spacing: 2px;
  }

  &.open {
    transition: max-height .2s ease-in-out, opacity .2s ease-in-out, visibility .2s ease-in-out;

    .arrow-down-close {
      &:before {
        transform: translateX(7px) rotate(-45deg);
      }

      &:after {
        transform: translateX(-7px) rotate(45deg);
      }
    }
  }

}


.accordion__content {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  transition: grid-template-rows .2s ease-in-out;
}

.accordion__content div {
  padding-left: 60px;
  padding-right: 60px;
  word-wrap: break-word;

  @include m-520 {
    padding-left: 20px;
    padding-right: 20px;
  }

  p {
    font-size: 1rem;
    line-height: 1.3;
    letter-spacing: 1.6px;
    color: var(--violet);
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s ease-in-out, visibility .2s ease-in-out, color .5s ease-in-out;
  }

  p:last-child {
    margin-bottom: 30px;
  }
}

.accordion__content.open {
  grid-template-rows: 1fr;

  p {
    visibility: visible;
    opacity: 1;
    color: var(--white-color-06);
    transition: opacity .2s ease-in-out, visibility .2s ease-in-out, color .5s ease-in-out;
  }
}