.footer-section {
  padding: 60px 0 20px;
  background-color: var(--dark-color);
  color: var(--white-color);
}

.footer {
  display: flex;
  width: 100%;

  @include m-960 {
    flex-wrap: wrap;
  }

  // .footer__item

  &__item {
    @include m-960 {
      flex: 100%;
    }
  }

  &__item:nth-child(1) {
    @include m-960 {
      text-align: center;
    }
  }

  &__item:nth-child(2) {
    flex: 1;
    padding-left: 80px;

    @include m-960 {
      margin-top: 40px;
      padding-left: 0px;
    }

  }

  // .footer__content

  &__content {
    font-size: .8rem;
    margin-top: 18px;

    a,
    p {
      padding: 0;
      margin: 0 0 8px 0;
      line-height: 120%;
    }

    a {
      opacity: .6;
      color: var(--pink-color);
      @include hover {
        opacity: 1;
      }
    }
  }

  // .footer__nav

  &__nav {
    width: 100%;
    padding-top: 85px;
    line-height: 120%;
    letter-spacing: 1.4px;

    @include m-1070 {
      padding-top: 0;
    }

    h2 {
      font-weight: 400;
      font-size: 1rem;
      opacity: .6;
      letter-spacing: 2px;
      margin-bottom: 12px;
    }

    ul {
      display: flex;

      @include m-1070 {
        flex-wrap: wrap;
      }

      &>li {
        flex: 0 0 25%;
        width: 25%;
        padding-left: 1rem;

        @include m-1070 {
          flex: 0 0 50%;
          width: 50%;
        }
      }

      li ul {
        flex-direction: column;
        margin-bottom: 20px;

        li {
          padding: 0;
          width: 100%;
        }
      }

      li a {
        display: inline-block;
        padding: 5px 0;
        font-weight: 400;
        font-size: .8rem;
        line-height: 120%;

        @include hover {
          color: var(--pink-color);
        }
      }
    }

  }
}


.footer-logo {}

.footer-bottom {
  margin-top: 30px;


  &__content {
    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 30px;
      line-height: 120%;

      @include m-460 {
        flex-wrap: wrap;
        gap: 10px;
      }

      a {
        color: var(--pink-color);
        transition: color .3s ease-in-out;

        @include hover {
          color: var(--pink-light-color);
        }
      }
    }

  }
}
