.advantages {
  &__list {
    ul {
      display: grid;
      gap: 1rem;
      grid-template-columns: 1fr 1fr 1fr;

      @include m-960 {
        grid-template-columns: 1fr 1fr;
      }

      @include m-650 {
        grid-template-columns: 1fr;
      }

      li {
        background-color: var(--blue-light-color-03);
        color: var(--second-color-07);
        border: 3px solid var(--white-color);
        padding: 2rem;
        border-radius: var(--br-2);
        display: flex;
        justify-content: center;
        flex-direction: column;
        transition: all .3s ease-in-out;
        position: relative;

        &::before {
          content: '';
          position: absolute;
          width: 100px;
          height: 100px;
          right: -40px;
          top: -30px;
          background: url('../img/svg/icon.svg') top / contain no-repeat;
          transform: scale(0);
          transition: all .3s ease-in-out;
        }


        @include hover {
          box-shadow: var(--box-shadow-blue);
          border: 3px solid var(--blue-light-color);
          background-color: var(--white-color);

          &::before {
            transform: scale(1);
          }

        }

        &:first-child {
          box-shadow: var(--box-shadow-blue);
          border: 3px solid var(--blue-light-color);
          background-color: var(--white-color);

          &::before {
            transform: scale(1);
          }
        }

        h3,
        h4 {
          font-size: 1.3rem;
          letter-spacing: 1.6px;
          color: var(--second-color);
        }
      }
    }
  }
}

.main__advantages-section {
  padding: 80px 0;
}
