// ============ Закрыть модал ================
.close-container {
  position: relative;
  margin: auto;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 5;
}

.leftright {
  height: 4px;
  width: 50px;
  position: absolute;
  margin-top: 24px;
  background-color: var(--second-color);
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all .3s ease-in;
}

.rightleft {
  height: 4px;
  width: 50px;
  position: absolute;
  margin-top: 24px;
  background-color: var(--second-color);
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all .3s ease-in;
}

.close-container:hover .leftright {
  transform: rotate(-45deg);
  background-color: var(--second-bright-color);
}

.close-container:hover .rightleft {
  transform: rotate(45deg);
  background-color: var(--second-bright-color);
}

// ============ Закрыть модал ================

// ============ Модальное окно ============
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  // transition: all 3s ease-in-out;

  &--open {
    display: block;
    display: flex;
    align-items: start;
    justify-content: center;
  }
}

.modal-content {
  margin: 10% 1rem;
  max-width: 720px;
  width: 100%;
  background-color: var(--grey-color);
  border-radius: var(--br);
  box-shadow: var(--box-shadow-light);
  position: relative;
  display: flex;
  overflow: hidden;
  pointer-events: auto;

  @include m-600 {
    flex-direction: column-reverse;
  }

  &__left {
    padding: 60px 30px;
    flex: 1.5;
  }

  &__right {
    flex: 2;
    font-size: 0;
    overflow: hidden;

    @include m-600 {
      flex: 300px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top center;
    }

  }


  &__top {
    margin-bottom: 30px;
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    margin: 0;
    padding: 0;
    transition: transform .3s ease-in-out;
  }
}

.modal-open {
  overflow: hidden;
}

