//  ============ Форма ============

/* Стили для радио кнопок и чекбокса */
input[type="radio"],
input[type="checkbox"] {
  margin-right: 5px;
}

.input-label {
  font-size: .8rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.7px;
  color: var(--dark-color-06);
  transition: 0.3s;
  &--req{
    &::after{
    content: '*';
    color: var(--red-color);
    margin-left: 6px;
  }
  }

}

.input-block {
  display: flex;
  flex-direction: column;
  padding: 10px 10px 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 20px;
  transition: 0.3s;
  position: relative;

  input {
    outline: 0;
    border: 0;
    padding: 4px 0 0;
    font-size: 1rem;
    background-color: transparent;
    color: var(--dark-color);

    &::placeholder {
      color: var(--dark-color);
      opacity: 1;
    }
  }

    // .input-block:focus-within
  &:focus-within, .input-block--active {
    border-color: var(--second-color-07);

    .input-label {
      color: var(--dark-color-04);
    }
  }
}

.form__button {
  margin-top: 1.4rem;
}

.form__pers {
  font-size: .8rem;
  line-height: 120%;
  margin-top: 1.2rem;

  a {
    color: var(--second-color);
    text-decoration: underline;
    transition: color .3s ease-in-out;

    @include hover {
      color: var(--second-bright-color);
    }
  }
}

.form-select {
  cursor: pointer;
  color: var(--dark-color);
  opacity: 1;
  position: relative;
  text-align: left;
  margin-top: 6px;

  &::after {
    content: '';
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-width: 10.4px 6px 0 6px;
    border-color: #79849B transparent transparent transparent;
    border-style: solid;
    pointer-events: none;
  }

  // .form-select__list

  &__list {
    display: none;
    position: absolute;
    // left: 0;
    right: 0;
    top: 80px;
    background-color: var(--grey-color);
    overflow: hidden;
    border-radius: 6px;
    width: auto;
    // height: 0;
    padding: 10px 10px 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    // margin-bottom: 20px;
    transition: 0.3s;
    z-index: 1;


    // .form-select__list--visible
    &--visible {
      display: block;
    }
  }

  // .form-select__list-item

  &__list-item {
    padding: 6px 0;
    cursor: pointer;
    display: flex;
    gap: 10px;
    align-items: center;
  }

  // .form-select__input-hidden

  &__input-hidden {
    display: none;
  }
}




