.tariffs-section {
  margin-bottom: 80px;


  &__container {
    position: relative;
    width: 100%;
  }

  &__decor {
    position: absolute;
    display: block;
    background-color: var(--blue-light-color-03);
    width: calc(100% - 2*(var(--container-offset)));
    height: 30rem;
    top: -60px;
    left: 0;
    border-radius: var(--br);
    z-index: -1;
    margin-left: var(--container-offset);
    // margin: 0 auto;
    // margin: 0 var(--container-offset);

    &::before {
      content: "";
      position: absolute;
      background: url(../img/svg/orn.svg) 0px 0px / contain no-repeat;
      width: 280px;
      height: 280px;
      left: -150px;
      top: 50px;
      transform: rotate(40deg);
      opacity: .5;
      user-select: none;
      z-index: -1;
    }

    &::after {
      content: "";
      position: absolute;
      background: url(../img/svg/orn-8.svg) 0px 0px / contain no-repeat;
      width: 280px;
      height: 280px;
      right: -50px;
      top: -100px;
      transform: rotate(40deg);
      opacity: .5;
      user-select: none;
      z-index: -1;
    }
  }

  &__title {
    margin-bottom: 2rem;
    text-align: center;
    padding: 0 2rem;
    position: relative;



    h3 {
      font-size: clamp(1.3rem, 1.4107rem + 0.2857vw, 1.6rem) !important;
      color: var(--second-color);
      margin-top: .6rem;
    }
  }
}

.card-price {

  // .card-price__list

  &__list {
    display: flex;
    justify-content: center;
    gap: 1.5rem;

    @include m-960 {
      overflow-x: auto;
      justify-content: start;
      padding: 0 2rem 3rem;
      gap: 2rem;
    }
  }

  // .card-price__item

  &__item {
    flex: 0 1 30%;
    padding: 3.5rem 2rem 2rem;
    background-color: var(--white-color);
    border-radius: var(--br-2);
    border: 1px solid #EBF4FF;
    box-shadow: 0px 2px 25px 0px rgba(76, 106, 170, 0.22);
    position: relative;


    @include m-960 {
      padding: 3rem 1rem 2rem;
      flex: 0 0 300px;
    }

    @include m-480 {
      flex: 0 0 260px;
    }
  }

  // card-price__sub
  &__sub {
    display: block;
    position: absolute;
    top: 1rem;
    right: 0;
    color: var(--white-color);
    background-color: var(--second-bright-color);
    height: 31px;
    display: flex;
    align-items: center;
    font-size: 0.8rem;
    padding: 0 10px;
    letter-spacing: 1.3px;

    &::before {
      content: '';
      display: block;
      position: absolute;
      background: url("../img/svg/popular-sec.svg") center/cover no-repeat;
      width: 13px;
      height: 31px;
      top: 0;
      left: -11px;
      transform: rotate(180deg);
    }

    &--red {
      background-color: var(--red-color);

      &::before {
        background: url("../img/svg/popular.svg") center/cover no-repeat;
      }
    }

    &--moln {
      &::after {
        content: '';
        display: block;
        background: url("../img/svg/moln-2.svg") center/contain no-repeat;
        width: 20px;
        height: 22px;
        margin-left: 6px;
      }
    }


  }

  // .card-price__title

  &__title {
    text-align: center;
  }

  // .card-price__desc

  &__desc {
    text-transform: uppercase;
    display: block;
    color: var(--dark-color-04);
    font-size: 0.8rem;
    margin-bottom: 6px;
  }

  ul ul {
    margin: 1rem 0;
    font-size: 0.9rem;

    li {
      display: flex;

      i {
        font-size: 1.2rem;
        margin-right: .5rem;
        transition: all .3s ease-in-out;
        font-weight: 600;
        color: var(--second-bright-color);
      }

      @include hover {
        i {
          transform: scale(1.2);
        }
      }

      i.bx-x {
        color: var(--red-color);
      }
    }
  }

  // .card-price__course-price

  &__course-price {
    font-size: 1.5rem;
    color: var(--second-color);
    font-weight: 600;
    text-align: center;
    margin-top: 1rem;

    span {
      font-weight: 400;
      font-size: .9rem;
      color: var(--blue-dark-color);
      display: block;
      max-width: 200px;
      margin: 0 auto;
      line-height: 120%;
      letter-spacing: 1.5px;
      margin-top: 6px;
    }
  }

  // .card-price__course-btn

  &__course-btn {
    text-align: center;
    margin: 1.5rem auto 0;
  }
}


[data-card] {
  position: relative;

  &::before {
    position: absolute;
    top: 10px;
    right: 10px;
    content: attr(data-card);
    color: var(--white-color);
    background-color: var(--red-color);
    padding: 4px 10px;
    font-size: 0.8rem;
    border-radius: var(--br-3);
  }

}

[data-card="star"] {
  &::before {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    content: '';
    top: 6px;
    right: 6px;
    background-color: transparent;
    background: var(--second-bright-color) url("../img/svg/moln-2.svg") 8px 6px/23px no-repeat;
  }
}



.price-card {

  // .price-card__list

  &__list {


    &>li {
      border-radius: var(--br);
      border: 3px solid var(--blue-dark-color);
      margin-bottom: 2rem;
      transition: all .3s ease-in-out;

      @include hover {
        border: 3px solid var(--violet);

        .price-card__desc {
          background-color: var(--violet);
        }
      }
    }
  }

  // price-card__header
  &__header {
    display: grid;
    grid-template-columns: 1fr 3fr;
    text-align: center;

    @include m-860 {
      grid-template-columns: 1fr 2fr;
    }

    @include m-680 {
      display: block;
    }
  }

  // price-card__title{
  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;

    h4 {
      font-size: 2rem;
      line-height: 120%;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      @include m-560 {
        font-size: 1.7rem;
      }
    }
  }

  // price-card__text
  &__text {
    color: var(--dark-color-06);
    text-align: left;
    flex: 1;

    @include m-680 {
      text-align: center;
    }

    p {
      font-size: 0.9rem;
      margin-bottom: 0.4rem;

      @include m-680 {
        font-size: 1rem;
      }
    }
  }

  // price-card__desc
  &__desc {
    color: var(--white-color);
    font-weight: 400;
    font-size: .9rem;
    background-color: var(--blue-dark-color);
    padding: 6px 14px;
    border-radius: 0 0 var(--br-3) var(--br-3);
    display: block;
    max-width: 300px;
    line-height: 120%;
    transition: all .3s ease-in-out;
    margin: 0 auto 1rem;

    @include m-680 {
      border-radius: var(--br-3);
    }

  }

  // .price-card__img

  &__img {
    padding: 1rem 0 0 1rem;

    @include m-680 {
      padding: 1rem;
    }

    img {
      border-radius: var(--br);
      height: 100%;

      @include m-650 {
        width: 300px;
      }
    }
  }

  // .price-card__content

  &__content {
    padding: 2rem;

    button {
      @include m-768 {
        margin: 0 auto;
      }
    }

    h5 {
      font-weight: 600;
      font-size: 1.3rem;
      color: var(--second-color);

      @include m-768 {
        text-align: center;
      }
    }

    ul {
      display: flex;
      gap: 2rem;

      @include m-680 {
        display: block;
      }

      li {
        flex: 1;
        margin: 1rem 0;
        background-color: transparent;
        border-radius: var(--br-3);
        transition: all .2s ease-in-out;
        position: relative;
        width: 100%;

        @include m-680 {
          margin-bottom: 3rem;
        }


        ul {
          display: block;
          margin: 1.3rem 0 2rem;

          @include m-680 {
            display: flex;
            flex-wrap: wrap;
            gap: 0;
          }

          @include m-520 {
            display: block;
          }

          li {
            padding: 0;
            margin: 0;
            padding: .05rem;
            margin-bottom: .5rem;
            margin-top: .5rem;
            font-size: .9rem;
            display: flex;
            align-items: center;

            @include m-680 {
              padding: 0.2rem 0;
              margin-bottom: 0;
              margin-top: 0;
              flex: 0 0 50%;
            }

            i {
              font-size: 1.4rem;
              margin: 0 .9rem;
            }

          }


        }

      }
    }
  }

  // price-card__course-price

  &__course-price {
    font-size: 2.3rem;
    margin: 2rem 0;
    font-weight: 700;
    color: var(--second-color);

    @include m-768 {
      text-align: center;
    }

    span {
      font-weight: 400;
      font-size: 1.1rem;
      color: var(--blue-dark-color);

      &::before {
        content: '/ ';
      }
    }
  }

  // .price-card__course-btn
  &__course-btn {
    @include m-768 {
      display: flex;
      justify-content: center;
    }

  }
}
