.tutor-section {

  // .tutor-section__sup

  &__sup {
    font-size: 0.8rem;
    text-transform: uppercase;
    color: var(--dark-color-06);
    letter-spacing: 1.1px;

    @include m-768 {
      text-align: center;
    }
  }

  // .tutor-section__title

  &__title {
    margin-bottom: 1.4rem;

    @include m-768 {
      margin-bottom: 2rem;
    }
  }
}

.main {

  // .main__tutor-section

  &__tutor-section {
    margin: 60px 0;
  }
}

.tutor {
  --width: 450px;
  --width-dotts: 280px;

  @include m-1040 {
    --width: 350px;
    --width-dotts: 240px;
  }

  position: relative;

  @include m-700 {
    text-align: center;
  }

  &::before {
    content: '';
    position: absolute;
    background: url(../img/svg/dotts.svg) 0px 0px / contain no-repeat;
    width: var(--width-dotts);
    height: var(--width-dotts);
    right: calc(var(--width) - var(--width-dotts) / 2);
    top: -50px;
    opacity: .5;
    user-select: none;
    z-index: -1;

    @include m-700 {
      right: 20px;
    }
  }

  img {
    border-radius: var(--br);
    float: right;
    max-width: var(--width);
    width: 100%;
    margin: 0 0 1rem 2rem;

    @include m-700 {
      margin: 0 auto 1rem;
      float: none;
      text-align: center;
    }
  }


  h3 {
    color: var(--second-color);
    font-size: 1.1rem;
    margin-top: 1.3rem;
  }

  p {
    margin-bottom: 10px !important;

    @include m-700 {
      text-align: left;
    }
  }

  ol {
    padding: 0;
    counter-reset: item;
    list-style-type: none;
    display: flex;
    gap: 1rem;
    margin: 3rem 0 0;

    @include m-480 {
      display: block;
    }

    li {
      margin-bottom: 0.7rem;
      text-align: center;
      flex: 0 1 200px;
      position: relative;
      font-size: 0.9rem;
      line-height: 120%;

      @include m-480 {
        margin-bottom: 3rem;
        font-size: 1rem;
      }

      &::before {
        display: block;
        content: counter(item, decimal-leading-zero) " ";
        counter-increment: item;
        color: var(--grey-dark-color);
        text-align: center;
        font-size: 6rem;
        margin-bottom: 4rem;
      }

      &::after {
        content: "";
        display: block;
        background: url("../img/svg/sertificate.svg") 0 0 / contain no-repeat;
        text-align: center;
        width: 50px;
        height: 50px;
        position: absolute;
        top: 30px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}
