.thanks-section {
  background-color: #2F3242;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 100%;
    width: 100%;
    height: 80px;
    background-color: #2F3242;
  }

  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    gap: 60px;

    @include m-680 {
      flex-direction: column-reverse;
      gap: 10px;
      padding: 20px 0;
    }
  }

  &__svg {
    flex: 0 0 380px;
    width: 380px;

    @include m-860 {
      flex: 0 0 300px;

      svg {
        width: 300px;
      }
    }

    @include m-680 {
      text-align: center;

      svg {
        width: 180px;
        height: 280px;
      }
    }
  }
}




.message-box {
  flex: 0 0 500px;
  width: 500px;
  color: var(--white-color);
  text-align: center;

  @include m-960 {
    flex: 1;
    width: 100%;
  }

  h1 {
    font-size: clamp(40px, 5vw, 60px);
    line-height: 46px;
    margin-bottom: 40px;
  }

  p {
    font-size: clamp(20px, 3vw, 30px);
    font-weight: 400;
  }
}

.message-box__btns .action-link-wrap {
  margin-top: 40px;
  display: flex;
  align-items: center;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
}

#Polygon-1,
#Polygon-2,
#Polygon-3,
#Polygon-4,
#Polygon-4,
#Polygon-5 {
  animation: float 1s infinite ease-in-out alternate;
}

#Polygon-2 {
  animation-delay: .2s;
}

#Polygon-3 {
  animation-delay: .4s;
}

#Polygon-4 {
  animation-delay: .6s;
}

#Polygon-5 {
  animation-delay: .8s;
}

@keyframes float {
  100% {
    transform: translateY(20px);
  }
}
