.carousel {
  overflow: visible;

  &__list {
    padding: 4rem 0 4rem;

  }

  &__item {
    aspect-ratio: 3/2.3;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    border-radius: var(--br);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 3px 15px 2px, rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;

    @include m-960 {
      aspect-ratio: 1/1;
    }

    @include m-560 {
      aspect-ratio: 2.3/3;
    }
  }

  &__box {
    width: 100%;
    height: 100%;
    position: relative;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: var(--tr-6);
    z-index: -1;
    background-color: var(--second-color-bg);
    position: relative;

    &::after {
      content: '';
      position: absolute;
      background: url("../img/svg/orn.svg") 0 0 / contain no-repeat;
      width: 280px;
      height: 280px;
      right: -50px;
      bottom: -50px;
      transform: rotate(40deg);
      opacity: 0.2;

      // @include m-1070 {
      //   width: 140px;
      //   height: 140px;
      //   right: -20px;
      //   bottom: -20px;
      // }
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(160deg, transparent 40%, var(--second-color-07) 70%, var(--violet-07) 100%);
      transition: var(--tr-6);
    }

    &--orn-2 {
      &::after {
        background: url("../img/svg/orn-2.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-3 {
      &::after {
        background: url("../img/svg/orn-3.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-4 {
      &::after {
        background: url("../img/svg/orn-4.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-5 {
      &::after {
        background: url("../img/svg/orn-5.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-6 {
      &::after {
        background: url("../img/svg/orn-6.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-7 {
      &::after {
        background: url("../img/svg/orn-7.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-8 {
      &::after {
        background: url("../img/svg/orn-8.svg") 0 0 / contain no-repeat;
      }
    }

    &--orn-9 {
      &::after {
        background: url("../img/svg/orn-9.svg") 0 0 / contain no-repeat;
      }
    }

    img {
      transform: scale(1);
      transition: var(--tr-6);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
  }

  &__item,
  &__item * {
    transition: var(tr-6);
    user-select: none;
  }

  &__contents {
    display: flex;
    flex-direction: column-reverse;
    justify-content: start;
    min-height: 200px;
    padding: 2rem 3rem;
    z-index: 2;
    background-image: radial-gradient(ellipse at 0px 0px, rgba(0, 0, 0, 0.3) 20%, transparent 50%);
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
    color: var(--white-color);

    .carousel__title {
      color: var(--white-color);
      font-size: 2.2rem;
      font-weight: 600;
      letter-spacing: 1.5px;
      text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
      transition: font-size .3s ease-in-out;
    }

    .carousel__desc {
      font-size: .875em;
      letter-spacing: 1.25px;
      text-transform: uppercase;
      color: transparent;
      background: linear-gradient(270deg, var(--white-color), var(--pink-color));
      background-clip: text;
      -webkit-background-clip: text;
      opacity: 0.85;
      text-wrap: balance;
      margin-bottom: 0.5rem;
    }
  }
}


.carousel__contents .carousel__link,
.carousel__contents .carousel__desc,
.carousel__contents .carousel__title {
  // margin: 0;
  line-height: 1.1;
}

.carousel__contents .carousel__link,
.carousel__contents .carousel__desc {
  opacity: 0;
  transform: translateX(-100%);
  transition-duration: 1s;
}

.carousel__contents .carousel__link {
  margin-top: 50px;
}

.carousel__item.swiper-slide-active {

  .carousel__image {
    &::before {
      background: transparent;
      transition: var(--tr-6);
    }

    img {
      transform: scale(1.4);
      transition: var(--tr-6);
    }
  }

  .carousel__contents .carousel__link,
  .carousel__contents .carousel__desc {
    transform: translateX(0px);
    transition-duration: 0.66s;
    opacity: 1;
  }

  .carousel__contents .carousel__title {
    transition-delay: 0.2s;
    font-size: 1.75rem;
  }

  .carousel__contents .carousel__link {
    transition-delay: 0.2s;
  }

  .carousel__contents .carousel__desc {
    opacity: 0.85;
    transition-delay: 0.05s;
  }
}

.swiper-button-prev,
.swiper-button-next {
  background-color: var(--second-color-07);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: all .3s ease-in-out;

  @include m-1070 {
    transform: scale(.7);
  }

  &::after {
    color: var(--white-color);
  }

  @include hover {
    background-color: var(--second-color);
  }
}
